<template>
<CRow>
    <CCol col="12" class="mb-5">
        <CRow>
            <CCol col="8">
                <h3 class="font-weight-bold text-uppercase mb-1">Code Pembukuan Formulir</h3>
                <p class="text-primary text-sm-left mb-0">{{$options.identitas[0].instansi}}</p>
                <p class="text-primary text-sm-left">{{$options.identitas[0].provinsi}}</p>
            </CCol>
            <CCol>
                <div class="text-right mt-5 mr-5">
                    <router-link :to="{ path:'/pembukuan/code'}" class="btn btn-primary text-white">Halaman Code</router-link>
                </div>
            </CCol>
        </CRow>
    </CCol>
    <CCol col="12">
        <div class="wrapperModul p-3">
            <CRow>
                <CCol sm="2">
                    <CInput label="Nama" v-model.trim="$v.code.nama.$model" :isValid="$v.code.nama.$dirty ? !$v.code.nama.$error : null" placeholder="Ketik Nama" />
                </CCol>
                <CCol sm="2">
                    <CInput label="Kode" v-model.trim="$v.code.code.$model" :isValid="$v.code.code.$dirty ? !$v.code.code.$error : null" placeholder="Ketik Kode" />
                </CCol>
                <CCol sm="2">
                    <CSelect :options="optionsInstansi" :value.sync="$v.code.instansi_id.$model" :lazy="true" :isValid="$v.code.instansi_id.$dirty ? !$v.code.instansi_id.$error : null" label="instansi" placeholder="Instansi" />
                </CCol>
                <CCol sm="3">
                    <CSwitch class="mr-1 mt-4 pt-1" color="primary" :checked.sync="code.active" v-model="code.active" />
                </CCol>
            </CRow>
            <CRow>
                <CCol col="12">
                    <CButton color="primary" class="text-white" @click="ToDatabase()">{{type}}</CButton>
                </CCol>
            </CRow>
        </div>
    </CCol>
</CRow>
</template>

<script>
import identitas from '@/containers/_identitas'
import tableta from '@/views/table/table'
import {
    required,
    email
} from 'vuelidate/lib/validators'
const axios = require('axios');
export default {
    identitas,
    components: {
        tableta
    },
    data() {
        return {
            code : {
                code_id : null,
                instansi_id : null,
                nama : null,
                code : null,
                active : null,
            },
            instansi : [],
            type: this.$route.params.type === 'edit' ? "update" : "insert",
        }
    },computed: {
        optionsInstansi() {
            return this.instansi.map((header, index) => {
                return {
                    label: header.instansi,
                    value: header.instansi_id,
                };
            });
        },
    },
    validations: {
        code: {
            instansi_id: {
                required
            },
            nama: {
                required
            },
            code: {
                required
            },

        }
    },
    created() {
        this.getData()
        this.getInstansi()
        this.getRoles()
    },
    methods: {
        getData() {
            axios.post(this.$options.identitas[0].url + '/nomor/code?type=dataById', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    e.data.code === "404" ? this.$router.push({
                        path: '/pages/404'
                    }) : (this.code = e.data.data)

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        getInstansi() {
            axios.post(this.$options.identitas[0].url + '/instansi?type=dataByAll', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    this.instansi = e.data.data

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        getRoles() {
            axios.post(this.$options.identitas[0].url + '/roles?type=dataByAll', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    this.roles = e.data.data

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        ToDatabase() {
            this.$v.$touch()
            if (!this.$v.$invalid) {
                axios.post(this.$options.identitas[0].url + '/nomor/code?type=' + this.type, {
                        data: this.code
                    })
                    .then((e) => {
                        this.$notify({
                            group: 'foo',
                            title: 'Information',
                            text: this.$route.params.type === 'edit' ? "Data Berhasil Di Ubah" : "Data Berhasil Di Tambahkan",
                        });

                        this.$router.push({
                            path: '/pembukuan/code'
                        })
                    })
                    .catch(function (error) {
                        // handle error
                        console.log(error);
                    })
            }
        },
    }

}
</script>

<style>
.wrapperModul {
    border: 1px solid #dfdfdf;
    border-radius: 5px;

}

.float {
    position: fixed;
    width: 120px;
    bottom: 80px;
    right: 40px;
    color: #FFF;
    text-align: center;
    box-shadow: 2px 2px 3px #999;
}
</style>
